.light-toggler {
  background-color: var(--bg-clr-alt);
  border-radius: 50%;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all var(--trans);
  margin-bottom: unset !important;
  right: 2rem;
  position: absolute;
  top: 5%;
  z-index: 99;
  width: 5rem;
  height: 5rem; }
  .light-toggler .icon {
    font-size: 1.5rem;
    color: var(--txt-clr);
    cursor: pointer; }
  .light-toggler:not(:last-of-type) {
    margin-bottom: 1rem; }
  .light-toggler:hover {
    background-color: var(--theme-clr);
    transform: scale(1.05); }
    .light-toggler:hover .icon {
      color: white; }
  .light-toggler.active {
    background-color: var(--theme-clr); }
  .light-toggler .icon {
    font-size: 2.5em; }

body.light .light-toggler.control:hover, body.light .light-toggler.control:active {
  background-color: var(--theme-clr); }

.controls {
  position: fixed;
  right: 2rem;
  top: 30%;
  z-index: 99; }
  .controls .control {
    background-color: var(--bg-clr-alt);
    border-radius: 50%;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all var(--trans); }
    .controls .control .icon {
      font-size: 1.5rem;
      color: var(--txt-clr);
      cursor: pointer; }
    .controls .control:not(:last-of-type) {
      margin-bottom: 1rem; }
    .controls .control:hover {
      background-color: var(--theme-clr);
      transform: scale(1.05); }
      .controls .control:hover .icon {
        color: white; }
    .controls .control.active {
      background-color: var(--theme-clr); }

body section.home .smileys {
  height: 100vh;
  width: 20vw;
  background: var(--grad-clr);
  padding: 2rem;
  position: absolute;
  clip-path: polygon(0 0, 100% 0, 70% 100%, 0% 100%);
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row-reverse;
  gap: 3rem; }
  body section.home .smileys .smile {
    position: relative;
    width: 50px;
    height: 50px;
    z-index: 2; }
    body section.home .smileys .smile input[type="radio"] {
      appearance: none; }
      body section.home .smileys .smile input[type="radio"]:hover ~ .face::after {
        border-radius: 0 0 1rem 1rem;
        height: 1rem;
        transform: translateX(-50%) translateY(0.65rem);
        width: 1.8rem; }
      body section.home .smileys .smile input[type="radio"]:hover ~ .face::before {
        height: 5px;
        transform: translate(0, 5px); }
    body section.home .smileys .smile .face {
      position: absolute;
      inset: 0;
      background: #333;
      border-radius: 50%;
      mix-blend-mode: lighten;
      transition: all var(--trans); }
      body section.home .smileys .smile .face::before, body section.home .smileys .smile .face::after {
        content: "";
        position: absolute;
        background-color: var(--bg-clr);
        opacity: 0.5;
        transition: all var(--trans); }
      body section.home .smileys .smile .face::before {
        border-radius: 50%;
        box-shadow: 22px 0 0 var(--bg-clr);
        height: 10px;
        left: 10px;
        top: 14px;
        width: 10px; }
      body section.home .smileys .smile .face::after {
        border-radius: 8px;
        bottom: 14px;
        height: 4px;
        left: 50%;
        transform: translateX(-50%);
        transition: all var(--trans);
        width: 20px; }
    body section.home .smileys .smile:nth-child(odd) input:hover ~ .face {
      background-color: #a07850;
      filter: drop-shadow(0 0 50px #a07850) drop-shadow(0 0 100px #a07850) drop-shadow(0 0 150px #a07850); }
    body section.home .smileys .smile:nth-child(even) input:hover ~ .face {
      background-color: #c35353;
      filter: drop-shadow(0 0 50px #c35353) drop-shadow(0 0 100px #c35353) drop-shadow(0 0 150px #c35353); }

body section.home .content {
  height: 100vh;
  z-index: 2;
  position: relative; }
  body section.home .content .me1 {
    align-self: center;
    max-height: 90vh;
    margin-inline: auto; }
  body section.home .content .text-content {
    align-self: center;
    padding-right: 10rem; }
    body section.home .content .text-content h1 {
      font-size: 3rem;
      font-weight: bold; }
      body section.home .content .text-content h1 span {
        color: var(--theme-clr); }
      body section.home .content .text-content h1 small {
        font-size: 50%; }
    body section.home .content .text-content p span {
      color: var(--theme-clr); }
    body section.home .content .text-content button {
      padding: 0.7rem 5rem 0.7rem 2rem;
      background-color: transparent;
      border: 1px solid var(--theme-clr);
      border-radius: 1.5rem;
      position: relative;
      margin-top: 2rem;
      transition: all var(--trans); }
      body section.home .content .text-content button:hover {
        background-color: var(--bg-clr-alt);
        transform: scale(1.05); }
        body section.home .content .text-content button:hover p {
          color: var(--theme-clr); }
        body section.home .content .text-content button:hover i {
          transform: rotate(-5deg);
          animation: up-Down 1s alternate infinite; }
      body section.home .content .text-content button p {
        display: initial;
        font-family: cursive;
        font-size: 1.3rem;
        font-weight: bold; }
      body section.home .content .text-content button i {
        border-radius: 50%;
        color: #fff;
        font-size: 1.5rem;
        position: absolute;
        right: -2px;
        top: 0px;
        padding: 15px;
        background-color: var(--theme-clr);
        transition: transform var(-trans); }

